@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&display=swap");

.container {
  max-width: 1110px;
  margin: 0 auto;
}

* {
  font-family: "Poppins", sans-serif;

  color: #ffffff;
}
body {
  background: #12141d;
}
.btn {
  width: 157px;
  height: 53px;
  background: #4080bf;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  a {
    text-decoration: none;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 27px;
    text-transform: capitalize;
  }
}

header {
  padding: 15px 0;

  nav {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    .brand {
      font-style: normal;
      font-weight: 800;
      font-size: 30px;
      line-height: 45px;
    }
    .nav-list {
      display: flex;
      width: 440px;
      justify-content: space-between;

      .nav-item {
        list-style: none;

        .nav-link {
          text-decoration: none;
          font-style: normal;
          font-weight: normal;
          font-size: 14px;
          line-height: 21px;
        }
      }
    }
  }
}
.head {
  margin: 43px 0 150px;
  padding: 207px 0 90px 363px;
  background-image: url(../img/head__bg.png);
  background-repeat: no-repeat;
  background-position: center;
  &__title {
    margin-bottom: 10px;
    h1 {
      font-style: normal;
      font-weight: 800;
      font-size: 61px;
      line-height: 92px;
    }
  }
  &__subtitle {
    margin-bottom: 30px;
    h2 {
      font-style: normal;
      font-weight: normal;
      font-size: 20px;
      line-height: 38px;
    }
  }
}
.section__about {
  margin-bottom: 120px;
  .about__title {
    width: 332px;
    margin-bottom: 40px;
    h3 {
      font-style: normal;
      font-weight: 800;
      font-size: 50px;
      line-height: 70px;
    }
  }
  .about {
    &__block {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
    }
    &__box {
      display: flex;
      max-width: 540px;
      width: 100%;
      margin-bottom: 30px;
    }
    &__img {
      margin-right: 30px;
    }
    &__text {
      h4 {
        font-style: normal;
        font-weight: 600;
        font-size: 28px;
        line-height: 42px;
        text-transform: capitalize;
      }
      p {
        font-style: normal;
        font-weight: 300;
        font-size: 16px;
        line-height: 30px;
        text-transform: capitalize;
      }
    }
  }
}
.section__spec {
  margin-bottom: 120px;
  .spec__title {
    font-style: normal;
    font-weight: 800;
    font-size: 50px;
    line-height: 83px;
    text-transform: capitalize;
    max-width: 600px;
    width: 100%;
    margin: 0 auto 50px;
    text-align: center;
  }
  .spec__block {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .spec__box {
    padding: 20px 20px 40px;
    max-width: 350px;
    width: 100%;
    background: #1e2028;
    border-radius: 10px;
    margin-bottom: 30px;
    img {
      margin-bottom: 55px;
    }
    h4 {
      font-style: normal;
      font-weight: 600;
      font-size: 24px;
      line-height: 36px;
      text-transform: capitalize;
      margin-bottom: 10px;
    }
    p {
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 28px;
      font-feature-settings: "salt" on, "liga" off;
      margin-bottom: 50px;
    }
    .spec__link {
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 20px;
      font-feature-settings: "salt" on, "liga" off;
      color: #a1c5e9;
      text-decoration: none;
      display: flex;
      align-items: center;
      img {
        margin-bottom: 0;
        margin-left: 20px;
      }
    }
  }
}

.section__project {
  margin-bottom: 120px;
  .project__head {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 50px;
  }
  .project__title {
    font-style: normal;
    font-weight: 800;
    font-size: 50px;
    line-height: 70px;
    max-width: 430px;
    width: 100%;
  }

  .link__all {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
    font-feature-settings: "salt" on, "liga" off;
    color: #a1c5e9;
    text-decoration: none;
    display: flex;
    align-items: center;
    img {
      margin-bottom: 0;
      margin-left: 20px;
    }
  }
  .project__block {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .project__box {
    max-width: 540px;
    width: 100%;
    margin-bottom: 30px;
    img {
      background: #1e2028;
      width: 100%;
      border-radius: 10px;
    }
  }
}

.section__team {
  margin-bottom: 175px;
  .team__title {
    font-style: normal;
    font-weight: 800;
    font-size: 50px;
    line-height: 75px;
    text-align: center;
    margin-bottom: 50px;
  }
  .team__block {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }
  .team__box {
    display: flex;
    align-items: flex-end;
    max-width: 500px;
    width: 100%;
    margin: 0 12.5px 25px;
    h4 {
      font-style: normal;
      font-weight: 500;
      font-size: 18px;
      line-height: 27px;
    }
    p {
      font-style: normal;
      font-weight: 300;
      font-size: 14px;
      line-height: 26px;
    }
  }
  .team__text {
    padding-bottom: 70px;
  }
  .block_l {
    .team__text {
      text-align: right;
      margin-right: 10px;
    }
  }
  .block_r {
    .team__text {
      text-align: left;
      margin-left: 10px;
    }
  }
}

.section__reviews {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 150px;
  .reviews__block {
    flex-basis: 50%;
  }

  .reviews__title {
    font-style: normal;
    font-weight: 800;
    font-size: 50px;
    line-height: 83px;
    max-width: 366px;
    margin-bottom: 10px;
  }
  .reviews__subtitle {
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 35px;
    font-feature-settings: "salt" on, "liga" off;
    max-width: 490px;
  }
  .reviews__slider {
    background: #1e2028;
    border-radius: 10px;
    max-width: 570px;
    width: 100%;
    max-height: 500px;
    height: 100%;
    padding: 37px 44px 56px 26px;
    .excerpt {
      display: flex;
      justify-content: end;
      opacity: 0.5;
      img {
        width: 80px;
        height: 68px;
        margin-bottom: 20px;
      }
    }
    .people {
      margin-bottom: 20px;
      img {
        width: 55px;
        height: 55px;
        border-radius: 10px;
        float: left;
        margin-right: 20px;
      }
      h4 {
        font-style: normal;
        font-weight: 600;
        font-size: 18px;
        line-height: 22px;
        margin-bottom: 5px;
      }
      p {
        font-size: 14px;
        line-height: 21px;
      }
    }
    p {
      opacity: 0.5;
      font-style: normal;
      font-weight: 300;
      font-size: 16px;
      line-height: 31px;
      font-feature-settings: "salt" on, "liga" off;
    }
  }

  /* Кружочки */
  .slider-dots {
    text-align: center;
    margin-top: 50px;
  }
  .slider-dots_item {
    cursor: pointer;
    height: 10px;
    width: 10px;
    margin: 0 5px;
    background-color: #8e8f93;
    border-radius: 10px;
    display: inline-block;
    transition: background-color 0.6s ease;
  }
  .active,
  .slider-dots_item:hover {
    background-color: #ffffff;
  }

  /* Анимация слайдов */
  .slider .item {
    -webkit-animation-name: fade;
    -webkit-animation-duration: 1.5s;
    animation-name: fade;
    animation-duration: 1.5s;
  }
  @-webkit-keyframes fade {
    from {
      opacity: 0.4;
    }
    to {
      opacity: 1;
    }
  }
  @keyframes fade {
    from {
      opacity: 0.4;
    }
    to {
      opacity: 1;
    }
  }
}

.section__offer {
  background: #1e2028;
  padding: 50px 0;
  .offer__title {
    max-width: 650px;
    width: 100%;
    margin: 0 auto;
    text-align: center;
    font-style: normal;
    font-weight: 800;
    font-size: 50px;
    line-height: 80px;
    margin-bottom: 35px;
  }
  .offer__btn {
    margin: 0 auto 50px;
  }
  .offer__block {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: 775px;
    width: 100%;
    margin: 0 auto;
  }
  .offer__box {
    text-align: center;
    border-left: 2px solid #ffffff;
    border-right: 2px solid #ffffff;
    max-width: 237px;
    width: 100%;
    h4 {
      font-style: normal;
      font-weight: bold;
      font-size: 28px;
      line-height: 40px;
      font-feature-settings: "salt" on, "liga" off;
    }
    h5 {
      margin: 0 auto;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 26px;
      font-feature-settings: "salt" on, "liga" off;
    }
  }
}

footer {
  margin-top: 50px;
  border-top: 1px solid #1e2028;
  padding: 50px 0 30px;
  .container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .footer__block {
    .brand {
      font-style: normal;
      font-weight: 800;
      font-size: 24px;
      line-height: 36px;
      margin-bottom: 10px;
    }
    .footer__title {
      font-style: normal;
      font-weight: 500;
      font-size: 18px;
      line-height: 27px;
      margin-bottom: 20px;
    }
    .copy {
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 26px;
      margin-bottom: 20px;
      max-width: 210px;
      width: 100%;
    }
    .social {
      img {
        margin-right: 10px;
      }
    }
    .list {
      padding: 0;
      margin: 0;
      &-item {
        list-style: none;
        padding: 0;
        margin: 0;
      }
      .link {
        text-decoration: none;
        font-style: normal;
        font-weight: 300;
        font-size: 16px;
        line-height: 24px;
        margin-bottom: 10px;
      }
    }
    form {
      position: relative;
      input {
        width: 255px;
        height: 40px;
        background: #1e2028;

        border-radius: 8px;
        border: none;
        z-index: -10;
        padding-left: 12px;
        &::placeholder {
          font-style: normal;
          font-weight: 300;
          font-size: 14px;
          line-height: 21px;
          font-feature-settings: "salt" on, "liga" off;
          color: #d9dbe1;
          opacity: 100;
          z-index: 555;
        }
      }
      label {
        position: absolute;
        right: 15px;
        top: 13px;
        cursor: pointer;
      }
    }
  }
}
